import { useQuery } from '@apollo/client';
import React, { FC } from 'react';
import { useTranslation } from 'react-i18next';
import { Discussion } from '../../typings';
import { GET_FEATURED_DISCUSSIONS } from './apollo/queries/discussions';

type Data = {
  featuredDiscussions: Discussion[];
};

const LiveDiscussionsList: FC = () => {
  const { t } = useTranslation('discussion');
  const { data } = useQuery<Data>(GET_FEATURED_DISCUSSIONS);

  return (
    <div>
      <h3>{t('title')}</h3>
      {data?.featuredDiscussions.map((item, index) => (
        <p key={index}>{item.title}</p>
      ))}
    </div>
  );
};

export default LiveDiscussionsList;
